.App {
    text-align: center;
    background-color: #ffb500;
    min-height: 100vh;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  .App-header {
    display: flex;
    flex-direction: column;
    justify-content: top;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  .logo, .logo:visited {
    color: #ffb500;
    background-color: #46271b;
    text-shadow: 5px 5px 0 #000000;
    font-family: Arial;
    padding-inline: 10px;
  }
  
  .logo span {
    justify-self: flex-start;
  }
  
  .Registration {
    align-items: center;
    margin-bottom: 10px;
  }
  
  button {
    margin-inline: 10px;
    margin-bottom: 10px;
  }